@import "@folio/stripes-components/lib/variables.css";

.resetPasswordButton {
  color: var(--primary);
  text-decoration: underline;
  cursor: pointer;
  font-size: var(--font-size-small);
  font-weight: var(--text-weight-bold);

  &:focus {
    outline: none;
  }

  &:disabled {
    color: var(--checkable-disabled-fill);
    cursor: not-allowed;
  }
}
